<template>
<table>
    <tr :key="i" v-for="(product,i) in productList">
        <td>{{ product.product_name }}</td>
        <td>{{ product.price }}</td>
        <td>{{ product.category }}</td>
    </tr>
</table>
</template>
<style scoped>
td { border:1px  solid #ddd; padding:5px;}
</style>
<script>

import ApiMixin from '../api.js';
export default{ 
    mixins:[ApiMixin],
    data(){
        return{
            productList:[]
        };
    },
    setup(){},
    created(){},
    async mounted(){
        this.productList = await this.$callAPI('https://ddb6fe25-13b1-41a2-a103-36b3fcfbb8bc.mock.pstmn.io/list');
        console.log(this.productList);
    },
    unmounted(){},
    methods:{}
}
</script>