<template>
    
    <h1>{{  fullName }}</h1>
    <button type="button" @click="changeName">변경</button>
</template>
<script>

export default{ 
    data(){
        return{
            firstName   : 'lee',
            lastName    : 'seungmin',
            fullName    : ''
        };
    },
    setup(){},
    created(){},
    mounted(){},
    unmounted(){},
    watch:{
        firstName(){
            this.fullName = this.firstName + ' ' + this.lastName;
        },
        lastName(){
            this.fullName = this.firstName + ' ' + this.lastName;
        }
    },
    methods:{
        changeName(){
            this.firstName = 'lim';
        }
    }
}
</script>
<style scoped>
    .container { width:100%; height:200px}
    .active {background:yellow;font-weight:600;}
    .text-red { color:red;}
</style>>