<template>
    <div>
        <table>
            <thead>
                <tr>
                    <th>제품명</th>
                    <th>가격</th>
                    <th>카테고리</th>
                    <th>배송료</th>
                </tr>
            </thead>
            <tbody>
                <tr :key="i" v-for="(product, i) in productList">
                    <td>{{ product.product_name }}</td>
                    <td>{{ product.price }}</td>
                    <td>{{ product.category }}</td>
                    <td>{{ product.delivery_price }}</td>
                </tr>
            </tbody>
        </table>
    </div>
</template>
<script>

export default{  
    data(){
        return{
            productList:[]
        };
    },
    setup(){},
    created(){
        this.getList();
    },
    mounted(){},
    unmounted(){},
    methods:{
        async getList(){
            this.productList = await this.$api("https://ddb6fe25-13b1-41a2-a103-36b3fcfbb8bc.mock.pstmn.io/list");
        }
    }
}
</script>
<style scoped>
    table { font-family:arial;  width:100%; }
    td,
    th { border:1px solid #ddd; padding:8px;}
</style>